.TypeEffect {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: 100;
}

.TypeEffect .dialog-text {
  font-family: Roboto-Mono;
  font-size: 2.5vw;
  color: #98a0ff;
}

/* The Cursor */
.TypeEffect .dialog-text::after {
  content: '';
  display: inline-block;
  position: relative;
  background: #98a0ff;
  width: 2px;
  height: 2.5vw;
  top: -1.25vw;
  -webkit-transform: translateY(50%);
      -ms-transform: translateY(50%);
          transform: translateY(50%);

  -webkit-animation: cursorAnim 0.25s linear 0s infinite normal;

          animation: cursorAnim 0.25s linear 0s infinite normal;
}
.TypeEffect.complete .dialog-text::after {
  -webkit-animation: completeAnim 0.1s linear 0s 1 normal forwards;
          animation: completeAnim 0.1s linear 0s 1 normal forwards;
}

@-webkit-keyframes cursorAnim {
  0% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  99% {
    opacity: 1;
  }
}

@keyframes cursorAnim {
  0% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  99% {
    opacity: 1;
  }
}

@-webkit-keyframes completeAnim {
  0% {
    height: 2.5vw;
  }
  100% {
    height: 0;
  }
}

@keyframes completeAnim {
  0% {
    height: 2.5vw;
  }
  100% {
    height: 0;
  }
}



