.HomeHeader {
  position: relative;
  height: 100vh;
  width: 100%;
  background: #f47761;
  z-index: 0;
}

/* The Foreground Content */
.HomeHeader .content {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 1750px;
  margin: auto;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
}

.HomeHeader .side {
  width: 50%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.HomeHeader .divider {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  width: 1px;
  height: 30%;
  background: #f39886;
}
.HomeHeader .divider::after,
.HomeHeader .divider::before {
  content: '';
  position: absolute;
  top: 100%;
  -webkit-transform-origin: 50% 0%;
      -ms-transform-origin: 50% 0%;
          transform-origin: 50% 0%;
  width: 1px;
  height: 25px;
  -webkit-transform: rotate(-130deg);
      -ms-transform: rotate(-130deg);
          transform: rotate(-130deg);
  background: #f39886;
}
.HomeHeader .divider::before {
  -webkit-transform: rotate(130deg);
      -ms-transform: rotate(130deg);
          transform: rotate(130deg);
}

.HomeHeader .title {
  font-family: Bungee;
  font-size: 54px;
  color: #737cd8;
  width: 80%;
}
.HomeHeader .title img {
  width: 3vw;
  position: relative;
  top: 0.2vw;
}

.HomeHeader .text-container {
  width: 75%;
}
.HomeHeader .header-text {
  font-family: Patua;
  font-size: 45px;
  color: white;
  margin-bottom: 15px;
}
.HomeHeader .header-text::after {
  content: '';
  position: relative;
  display: block;
  width: 100%;
  height: 5px;
  background: white;
  margin-top: 5px;
}
.HomeHeader .sub-text {
  font-family: Roboto-Mono;
  font-size: 25px;
  color: white;
  width: 90%;
}
.HomeHeader .sub-text b {
  font-weight: 700;
  text-decoration: underline;
}

.HomeHeader .instructions {
  font-family: Roboto Mono;
  font-size: 1vw;
  color: white;
  opacity: 0.5;
  position: absolute;
  left: 50%;
  bottom: 3.5%;
}

/* Animations */
.HomeHeader.anim .title {
  opacity: 0;
  -webkit-animation-name: fromLeftHeaderTitle;
          animation-name: fromLeftHeaderTitle;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
@-webkit-keyframes fromLeftHeaderTitle {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }
  100% {
    opacity: 1;
  }
}
@keyframes fromLeftHeaderTitle {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }
  100% {
    opacity: 1;
  }
}

.HomeHeader.anim .header-text {
  opacity: 0;
  -webkit-animation-name: fromRightHeaderText;
          animation-name: fromRightHeaderText;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}
@-webkit-keyframes fromRightHeaderText {
  0% {
    opacity: 0;
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }
  100% {
    opacity: 1;
  }
}
@keyframes fromRightHeaderText {
  0% {
    opacity: 0;
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }
  100% {
    opacity: 1;
  }
}

.HomeHeader.anim .sub-text {
  opacity: 0;
  -webkit-animation-name: fromRightHeaderText;
          animation-name: fromRightHeaderText;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 1.9s;
          animation-delay: 1.9s;
}

.HomeHeader.anim .divider {
  height: 0;
  -webkit-animation-name: growHeightHeaderDivider;
          animation-name: growHeightHeaderDivider;
  -webkit-animation-duration: 0.8s;
          animation-duration: 0.8s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 1.25s;
          animation-delay: 1.25s;
}
@-webkit-keyframes growHeightHeaderDivider {
  0% {
    height: 0;
  }
  100% {
    height: 78%;
  }
}
@keyframes growHeightHeaderDivider {
  0% {
    height: 0;
  }
  100% {
    height: 78%;
  }
}

.HomeHeader.anim .divider::after,
.HomeHeader.anim .divider::before {
  height: 0;
  -webkit-animation-name: arrowGrow;
          animation-name: arrowGrow;
  -webkit-animation-duration: 0.8s;
          animation-duration: 0.8s;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes arrowGrow {
  0% {
    height: 0;
  }
  100% {
    height: 20px;
  }
}
@keyframes arrowGrow {
  0% {
    height: 0;
  }
  100% {
    height: 20px;
  }
}

.HomeHeader.anim .instructions {
  -webkit-transform: translateX(-50%) translateY(25%);
      -ms-transform: translateX(-50%) translateY(25%);
          transform: translateX(-50%) translateY(25%);
  opacity: 0;
  -webkit-animation-name: fromBottom;
          animation-name: fromBottom;
  -webkit-animation-duration: 0.8s;
          animation-duration: 0.8s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
@-webkit-keyframes fromBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-50%) translateY(25%);
            transform: translateX(-50%) translateY(25%);
  }
  100% {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    opacity: 0.4;
  }
}
@keyframes fromBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-50%) translateY(25%);
            transform: translateX(-50%) translateY(25%);
  }
  100% {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    opacity: 0.4;
  }
}

/* Media Queries */
@media (min-width: 1575px) {
  .HomeHeader .title {
    font-size: 65px;
  }
  .HomeHeader .header-text {
    font-size: 55px;
  }
  .HomeHeader .sub-text {
    font-size: 30px;
  }
}
@media (min-width: 2000px) {
  .HomeHeader .title {
    font-size: 75px;
  }
}



