.Home {
  margin: auto;
}

.Home.anim {
  -webkit-animation: fadeIn 1s ease 0.5s 1 normal forwards;
          animation: fadeIn 1s ease 0.5s 1 normal forwards;
  opacity: 0;
}

/* Animations */
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



