.MyTech {
  position: relative;
  width: 100%;
  height: 100vh;
  background: #6177f4;
  z-index: 0;
}

.MyTech .vis-container {
  position: absolute;
  top: 0;
  left: 0%;
  width: 100%;
  height: 100%;

  /* Animation Default */
  opacity: 0;
}
.MyTech .vis-container canvas {
  position: absolute;
  z-index: 1;
  opacity: 1;
}

.MyTech .instructions {
  position: absolute;
  bottom: 25px;
  right: 25%;
  -webkit-transform: translateX(50%);
      -ms-transform: translateX(50%);
          transform: translateX(50%);
  color: white;
  opacity: 0.5;
  font-family: Roboto-Mono;
  z-index: 2;
}

.MyTech .left-side {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.MyTech .left-side .text-container {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-item-align: start;
      align-self: flex-start;
  margin-top: 100px;
  z-index: 2;
  border-radius: 10px;
  border: 2px solid #f47761;
  padding: 10px;
  padding-bottom: 25px;

  -webkit-box-shadow: 1px 1px #f47761, 2px 2px #f47761, 3px 3px #f47761, 4px 4px #f47761,
    5px 5px #f47761, 6px 6px #f47761, 7px 7px #f47761, 8px 8px #f47761,
    9px 9px #f47761;

          box-shadow: 1px 1px #f47761, 2px 2px #f47761, 3px 3px #f47761, 4px 4px #f47761,
    5px 5px #f47761, 6px 6px #f47761, 7px 7px #f47761, 8px 8px #f47761,
    9px 9px #f47761;

  /* Animation Default */
  opacity: 0;
}

.MyTech .left-side .title {
  position: relative;
  font-family: Fredoka;
  color: white;
  font-size: 50px;
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
  padding-bottom: 12px;
  text-align: center;
  margin-bottom: 20px;
  text-shadow: 1px 1px #6177f4, 2px 2px #6177f4, 3px 3px #6177f4,
    4px 4px #6177f4, 5px 5px #6177f4, 6px 6px #6177f4, 7px 7px #6177f4,
    8px 8px #6177f4, 9px 9px #6177f4;
}
.MyTech .left-side .title::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 105%;
  background: RGB(248, 122, 99);
  z-index: -1;
}

.MyTech .left-side .sub-text {
  color: white;
  text-align: left;
  font-family: Roboto-Thin;
  font-size: 18px;
  width: 90%;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

/* Animations */
.MyTech .text-container.anim .title {
  -webkit-animation-name: fromLeft;
          animation-name: fromLeft;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes fromLeft {
  0% {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
@keyframes fromLeft {
  0% {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

.MyTech .text-container.anim .sub-text {
  -webkit-animation-name: fromBelow;
          animation-name: fromBelow;
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes fromBelow {
  0% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
@keyframes fromBelow {
  0% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

.MyTech .text-container.anim {
  -webkit-animation-name: myTechFadeIn;
          animation-name: myTechFadeIn;
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.MyTech .vis-container.anim {
  opacity: 0;
  -webkit-animation-name: myTechFadeIn;
          animation-name: myTechFadeIn;
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
  -webkit-animation-duration: 0.8s;
          animation-duration: 0.8s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes myTechFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes myTechFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



